<template>
  <div class="park-item">
    <div class="park-item-title">
      <h2>{{ park.fullname }}</h2>
    </div>
    <div class="park-item-text">
      <p>{{ park.designation }}</p>
       <p>{{ park.description | truncate }}</p>
    </div>
    <div class="park-item-actions">
      <p><button class="pulse-boy" @click.prevent="savePark">Save Me</button></p>
      <p><router-link :to="{ name: 'park', params: { parkcode: park.parkCode || park.parkcode }}">Learn More</router-link></p>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";

export default {
  name: 'park-item',
  props: ['park'],
  filters: {
    truncate(value) {
      if (value.length > 90) {
        value = value.substring(0, 85) + '...';
      }
      return value;
    }
  },
  methods: {
    ...mapActions([
      "SAVE_PARK_ACTION"
    ]),
    savePark() {
      this.SAVE_PARK_ACTION(this.park);
    }
  }
}
</script>
