        </select>
        <input type="submit" value="Go">
      </form>
  </div>
</template>
<script>
import { mapActions } from "vuex";

export default {
  name: "parks-form-location",
  data() {
    return {
      state: null,
      loading: false,
      displayForm: true
    };
  },
  methods: {
     ...mapActions([
      "GET_STATE_PARKS"
    ]),
    submitLocation() {
      this.loading = true;
      this.GET_STATE_PARKS(this.state).then(
        () => {
          this.loading = false;
          this.displayForm = false;
          this.$emit('locationDone');
        }
      );
    }
  }
};
</script>
