<template>
  <div>
    <h1>Saved</h1>
    <ul class="saved-parks">
       <li class="saved-park" v-for="park in savedParks" v-bind:key="park.id">
         <a :href="park.url" target="_blank">{{ park.name }}</a>
         <button class="delete-btn" @click.prevent="remove(park.id)">X</button>
      </li>
    </ul>
  </div>
</template>
<script>
import { mapState } from 'vuex';
import { mapActions } from "vuex";

export default {
  name: "saved",
  computed: {
    ...mapState([
      'savedParks'
    ])
  },
  methods: {
    ...mapActions([
      "REMOVE_PARK_ACTION"
    ]),
    remove(id) {
      this.REMOVE_PARK_ACTION(id)
    }
  }
}
</script>
<style >
.saved-parks {list-style-type: none; margin: 0 auto; max-width: 600px;}
.saved-park {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  background: #fff;
  padding: 1rem;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.10);
  align-items: center;

}
.saved-park:hover {
    box-shadow: 0 15px 30px 0 rgba(0,0,0,.11), 0 5px 15px 0 rgba(0,0,0,.08);
}
.delete-btn {
    background: none;
    border: none;
    color: red;
    font-size: 1.5rem;
}

</style>

