<template>
  <div class="park-page">
     <div v-if="loading">Fetching park data</div>
     <div class="park-info" v-if="!loading">
       <div class="title">
        <button class="pulse-boy" @click.prevent="savePark(park)">Save</button>
        <h1>{{ park.fullName || park.fullname }}</h1>
       </div>

       <p>{{ park.description }}</p>

        <div v-if="park.weatherinfo || park.weatherInfo">
          <p class="label">Weather:</p>
          <p>{{ park.weatherinfo || park.weatherInfo }}</p>
        </div>
        <div v-if="park.latlong || park.latLong">
          <p class="label">Coordinates:</p>
          <p>{{ park.latlong || park.latLong }}</p>
        </div>
        <div v-if="park.directionsurl || park.directionsUrl">
          <p><a :href="park.directionsurl || park.directionsUrl" target="_blank">Directions</a></p>
        </div>

     </div>
     <div v-if="alerts.length > 0" class="park-alerts">
       <h1 v-if="!loading">Alerts for {{ park.name }}</h1>
       <div  class="alert" v-for="alert in alerts" v-bind:key="alert.id">
         <h4>{{ alert.title }}</h4>
         <p>{{ alert.category }}</p>
         <p>{{ alert.description }}</p>
         <a :href="alert.url" target="_blank">Learn More</a>
      </div>
     </div>
     <div class="park-alerts" v-else>
       <h1 v-if="!loading">Alerts for {{ park.name }}</h1>
        <h3>No Alerts</h3>
       </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";

export default {
  name: 'park',
  data() {
    return {
      loading: true,
      park: null,
      alerts: null
    };
  },
  created() {
    const apiurl = process.env.VUE_APP_PARKSAPI;
    const apiKey = process.env.VUE_APP_API_KEY;

    fetch(`${apiurl}/parks?parkCode=${this.$route.params.parkcode}&api_key=${apiKey}`)
      .then(response => response.json())
      .then(response => {
        this.park = response.data[0];
        this.loading = false;
      });
    fetch(`${apiurl}/alerts?parkCode=${this.$route.params.parkcode}&api_key=${apiKey}`)
      .then(response => response.json())
      .then(response => {
        this.alerts = response.data;
    });
  },
  methods: {
    ...mapActions([
      "SAVE_PARK_ACTION"
    ]),
    savePark() {
      this.SAVE_PARK_ACTION(this.park);
    }
  }
}
</script>
